.not-found-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
    font-family: Arial, sans-serif;
}

.not-found-h1 {
    font-size: 6rem;
    margin-bottom: 0;
}

.not-found-h2 {
    font-size: 2rem;
    margin-top: 0;
}

.not-found-p {
    font-size: 1.5rem;
    margin-bottom: 2rem;
}

.not-found-a {
    font-size: 1.2rem;
    text-decoration: none;
    color: #333;
    border: 2px solid #333;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    transition: all 0.3s ease;
}

.not-found-a:hover {
    background-color: #333;
    color: #fff;
}